<template>
  <div class="container">
    <div class="random-number" :style="{ fontSize: fontSize + 'px', fontFamily: fontFamily }">
      {{ randomNumber }}
    </div>
    <div class="text-representation">
      {{ textRepresentation }}
    </div>
    <div>
      <label class="easy">Easy</label>
      <input class="slider" type="range" min="500" max="5000" v-model="interval" 
@input="adjustInterval()" step="100">
      <label class="hard">Hard</label>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      randomNumber: 0,
      fontSize: 72,
      fontFamily: 'Arial Black',
      textRepresentation: '',
      interval: 3000
    }
  },
  mounted() {
    this.timer = setInterval(() => {
      this.randomNumber = Math.floor(Math.random() * 100)
      this.textRepresentation = this.numberToText(this.randomNumber)
    }, 6000 - this.interval)
  },
  beforeUnmount() {
    clearInterval(this.timer)
  },
  methods: {
    adjustInterval() {
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.randomNumber = Math.floor(Math.random() * 100)
        this.textRepresentation = this.numberToText(this.randomNumber)
      }, 6000 - this.interval)
    },
    numberToText(num) {
  const ones = [
    '零',
    '一',
    '二',
    '三',
    '四',
    '五',
    '六',
    '七',
    '八',
    '九'
  ]
  const tens = [
    '',
    '十',
    '二十',
    '三十',
    '四十',
    '五十',
    '六十',
    '七十',
    '八十',
    '九十'
  ]

  if (num === 0) return '零'
  if (num < 10) return ones[num]
  if (num === 10) return '十'
  if (num < 20) return `十${ones[num % 10]}`
  if (num % 10 === 0) return tens[Math.floor(num / 10)]
  return `${tens[Math.floor(num / 10)]}${ones[num % 10]}`
}
  }
}
</script>



<style>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.random-number {
  font-size: 72px;
  font-family: 'Arial Black', sans-serif;
  text-align: center;
}

.text-representation {
  font-size: 24px;
  font-family: 'Arial', sans-serif;
  text-align: center;
  margin-top: 20px;
}

.slider {
  width: 300px;
  margin: 20px 0;
}

.easy {
  float: left;
}

.hard {
  float: right;
}
</style>

